<template>
  <div>
      <h1 class="site-heading text-center text-white d-none d-lg-block">
    <span class="site-heading-lower">{{title}}</span>
  </h1>
    <section class="page-section cta">
      <div class="container">
        <div class="row">
          <div class="col-xl-9 mx-auto">
            <div class="cta-inner text-center rounded">
              <h2 class="section-heading mb-5">
                <span class="section-heading-upper">{{statusOpen}}</span>
                <span class="section-heading-lower">{{statusMessage}}</span>
              </h2>
              <ul class="list-unstyled list-hours mb-5 text-left mx-auto">
                <li class="list-unstyled-item list-hours-item d-flex">
                  Public Holidays
                  <span class="ml-auto">8.30:00 AM to 12:00:00 PM</span>
                </li>
                <li v-for="working in workingDays" :key="working.id" class="list-unstyled-item list-hours-item d-flex">
                  {{working.day}}
                  <span v-if="working.day != 'Sunday'" class="ml-auto">{{working.open}} to {{working.closed}}</span>
                  <span v-else class="ml-auto">Closed</span>
                </li>
              </ul>
              <p class="address mb-5">
                <em>
                  <strong>Sodnac Alouettes Avenue,</strong>
                  <br>
                  Sodnac Ave, Quatre Bornes, <br> Mauritius
                </em>
              </p>
              <p>{{ getShopAvailibility() }}</p>
              <p class="mb-0">
                <small>
                  <em>Call during working hours</em>
                </small>
                <br>
                (+230) 424-93-19
              </p>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
    name: "contact",
    data() {
        return {
            title: "Get In Touch",
            statusOpen: "",
            statusMessage: "",
            workingDays: [
              {
              id: 0,
              day: "Sunday",
              open: "",
              closed: ""
              },
              {
              id: 1,
              day:"Monday",
              open: "9:00:00 AM",
              closed: "5:00:00 PM"
              },
              {
              id: 2,
              day: "Tuesday",
              open: "9:00:00 AM",
              closed: "5:00:00 PM"
              },
              {
              id: 3,
              day: "Wednesday",
              open: "9:00:00 AM",
              closed: "5:00:00 PM"
              },
              {
              id: 4,
              day: "Thursday",
              open: "9:00:00 AM",
              closed: "5:00:00 PM"
              },
              {
              id: 5,
              day: "Friday",
              open: "9:00:00 AM",
              closed: "5:00:00 PM"
              },
              {
              id: 6,
              day: "Saturday",
              open: "9:00:00 AM",
              closed: "5:00:00 PM"
              },
              
            ]
        }
    },
    methods: {
      getShopAvailibility() {
        const timeTable = this.workingDays;
        const dayNow = new Date().getDay();
        const now = new Date();
        const todayDate = now.getMonth()+1+"/"+now.getDate()+"/"+now.getFullYear();        

        for (let i = 0; i < timeTable.length; i++) {
          const id = timeTable[i].id;
          if (dayNow == 1) {
            this.statusOpen = "Sorry";
            this.statusMessage = "We're Closed"
          }else{
                if (id == dayNow) {
                  let closed = new Date(""+todayDate+" "+timeTable[i].closed+"");
                  if(now.getTime() > closed.getTime()) {
                      this.statusOpen = "Sorry";
                      this.statusMessage = "We're Closed"
                  } else {
                      this.statusOpen = "Come In"
                      this.statusMessage = "We're Open"
                  }
                }
          }
      }  
    }
  }
}
</script>

