              },
        },
      methods: {
        showClient() {
          console.log(this.visibilityWC);
          if(this.visibilityWC == false) {
              this.errorMessage3 = true;
          }else {
            if ((this.getWCQuantityType() != null) && (this.getWCQuantity() != "") && (this.$v.$invalid != true)) {
                  this.customerSection = false;
                  this.customerSectionVisibility = true;
            }
          }  
        },
        backToOrder() {
          console.log(444);
          this.customerSectionVisibility = false;
        },
        hideModal() {
          this.$emit('closeModal');
        },
        validateQty(quantity) {
          this.qty = quantity;
          console.log(this.qty);
          //this.$v.qty.$touch();
          this.errorMessage = this.$v.$invalid;
          console.log(this.$v.$invalid)
        },
        WCtriggerCheck() {
            if (this.visibilityWC == false) {
                 this.visibilityWC = true;
                 this.errorMessage3 = false;
            }else{
              this.visibilityWC = false;
              this.quantityWC = '';
              this.wcNotes = '';
              this.selected = null;
            }
            
        },
        getWCQuantity() {
          if(this.quantityWC == "") {
              this.errorMessage2 = true;
          }else{
            this.errorMessage2 = false;
          }
          this.validateQty(this.quantityWC); 
          return this.quantityWC;
        },
        getWCQuantityType() {
          if (this.selectedQtyTypeWC == null) {
                this.errorMessage1 = true;
          }else{
            this.errorMessage1 = false;
          }
          return this.selectedQtyTypeWC;
        },
        getWCNotes() {
          return this.wcNotes;
        },
        getFullName() {
          return this.fullName;
        },
        getContactNum() {
          return this.telephone;
        },
        getEmailAddress() {
          return this.emailAddress;
        },
        chickenBasket() {
          let wholechickenOrder = this.wholechickenOrder();
          let customerData = this.customerData = [{name: this.getFullName(), email: this.getEmailAddress(), phone: this.getContactNum()}];
          this.$emit('closeModal');
          this.$emit('showAlert');
           console.log("Poulet entier "+JSON.stringify(wholechickenOrder));
           console.log("Customer data "+JSON.stringify(customerData));
        },
        wholechickenOrder() {
          if (this.visibilityWC == true) {
                this.orderWCData = [{id: "Whole Fresh Chicken", qtyType: this.selected, qty: this.getWCQuantity(), note: this.getWCNotes()}];
          }else {
            this.orderWCData = [];
          }

          return this.orderWCData;
        },
        pushOrder() {
          let data = {
            productType: "LA CASE",
            product: "PAPEL", 
            qtyType: "KG",
            qty: "50",
            notes: "Notebook",
            name: "VAR MM",
            email: "lp@gmail.com",
            phone: "569969779",
            orderId: "RT90607",
          };

          OrderProcessingService.createOrder(data)
            .then(response => {
              this.id = response.data.id;
              console.log(response.data);
            })
            .catch(e => {
            console.log(e);
            });
        },
        retrieveOrderList() {
          console.log(4444);
          return 1;
          // OrderProcessingService.getOrderLists()
          // .then(response => {
          //   this.orderLists = response.data;
          //   console.log(JSON.stringify(response.data));
          // })
          // .catch(e => {
          //   console.log(e);
          // });
        },
      },
      created: function() {
        this.retrieveOrderList();
      },
    }
    // console.log(JSON.stringify(data))
</script>

